<template>
    <div class="login">
      <div class="login-content" >
        <div class="login-main">
          <div class="login-header">
            <img src="dist/newLogo.png">
            <span>{{proName}}</span>
          </div>
          <div class="img-Position">
            <img src="dist/newComputer.png"/>
          </div>
          <div class="login-content">
            <div class="input-groups"  v-for="(item, $index) in inputOpt">
              <span class="label-show" >{{item.label}}：</span>
              <inputs :option="item.option" @getInput="getOption" @enter="enter"></inputs>
            </div>
            <!-- <div class="input-groups"  style="margin:3% 2em 2% 0">
              <input type="checkbox" class="checkBox"/>
              <label class="myCheckbox">记住密码</label>
            </div> -->
            <div class="button-div">
              <button @click="loginIn" class="button-operate">登 录</button>
            </div>

          </div>
        </div>
      </div>
      <div class="login-footer" v-html="footer">
      </div>
       <dialogs ref="loginDialog" type="loading"></dialogs>
       <dialogs ref="alert" type="alert"></dialogs>
       <encryptPass ref="encrypt"></encryptPass>
    </div>
</template>
<script>
    import encryptPass from './childComponent/encryptPass.vue'
    export default{
        data () {
            return {
              show: false,
              timeOut: '',
              imgList: [
                  {  url: 'dist/back1.png', desc: ''},
                  {  url: 'dist/back2.png', desc: ''},
                  {  url: 'dist/back3.png', desc: '' }
              ],
              userName: '',
              password: '',
              dialogMsg: '',
              inputOpt: [
                  {
                      icon: 'glyphicon glyphicon-user',
                      label: '用户名',
                      option: {
                         width:'20em',
                         type: 'normalInput',
                         key: 'userName',
                         placeHolder: '用户名'
                      }
                  },
                  {
                      icon: 'glyphicon glyphicon-lock',
                      label: '密    码',
                      option:  {
                        width:'20em',
                         type: 'passwordInput',
                         key: 'password',
                         placeHolder: '密码'
                      }
                  }
              ]
            }
        },
        components: {
          encryptPass
        },
        computed: {
            appVersion: function () {
                return (this.$store.state.systemConfig ? this.$store.state.systemConfig.version : (localStorage.systemConfig ?  JSON.parse(localStorage.systemConfig).version : '1.0.0')) + '.25';
            },
           footer: function () {

              let name = '';
              if (localStorage.copyright !== null &&  localStorage.copyright !== 'undefined' &&  typeof localStorage.copyright !== 'undefined') {
                  name = localStorage.copyright
               }
               let sign = this.$store.state.copyright !== null &&  this.$store.state.copyright !== 'undefined' &&  typeof this.$store.state.copyright !== 'undefined'
               return 'Copyright © '+ (sign ? this.$store.state.copyright : name) + ' ' +this.appVersion;
             },
            proName: function () {
                let name = '电能数据采集平台';
                if (localStorage.SystemName !== null &&  localStorage.SystemName !== 'undefined' &&  typeof localStorage.SystemName !== 'undefined') {
                    name = localStorage.SystemName
                }
                return name
            }
        },
        methods: {
          clearAllCookie() {  
               let keys = document.cookie.match(/[^ =;]+(?=\=)/g);  
               if(keys) {  
                  for(var i = keys.length; i--;)  
                     document.cookie = keys[i] + '=0;expires=' + new Date(0).toUTCString()  
                    }  
            },  
         
            enter: function (val) {
               this.getOption(val, this);
               if (this.userName !== '' && this.password !== ''){
                 this.loginIn()
               }
            },
            loginSucc: function (Data) {
               let that = this;
               localStorage.userInfo = JSON.stringify(Data);
               sessionStorage.userId = Data.userId;
               that.$store.state.cMenu = sessionStorage.cMenu = -1;
               that.$store.state.pMenu = sessionStorage.pMenu = -1;
               that.$store.state.topMenu =  sessionStorage.topMenu =  0;
               that.$store.state.currentParIndex = sessionStorage.currentParIndex = 0;
               that.$store.state.hasTree = sessionStorage.hasTree  = 0;
               sessionStorage.selectTab = JSON.stringify([
                {menuTitle: '系统首页', menuUrl: '/Main/Face', menuId: -1,  parentMenuId: -1, grandPMenuId: -1, hasTree: false}
                ]);
               that.$store.state.selectTab = JSON.parse(sessionStorage.selectTab);
               that.$router.push({path: '/Main/Face'});
            },
            loginIn: function () {
                let that = this;
                if(!that.show) {
                    that.$refs.alert.show({
                      msg: '系统只支持谷歌和火狐浏览器， 请切换浏览器重新登录！'
                   });
                    return
                }
                if (this.userName !== '' && this.password !== '') {
                 let pass = this.$refs.encrypt.encryptPass(this.password);
                    that.getRequest({
                        url: 'login',
                        param: {loginName: this.userName, password: pass},
                        method: 'post',
                        success: function (data) {
                          that.loginSucc(data.data);
                        },
                      error: function (data) {
                        that.$refs.alert.show({
                          msg: data.message || '登录出现未知错误，请重试'
                        });
                      }
                    }, true);
                } else {
                    that.$refs.alert.show({
                        msg: '用户名和密码都不能为空！！'
                    });
                }
            },
            getInfo: function () {
              let that = this;
              that.getRequest({
                url: 'system/querySystemConfig',
                method: 'post',
                success: function (data) {
                  let Data = data.data;
                  localStorage.systemConfig = JSON.stringify(Data);
                  that.$store.state.sysName = localStorage.SystemName = Data.sysName;
                  that.$store.state.copyright = localStorage.copyright = Data.copyright;
                  that.$store.state.balanceTime = Data.balanceDate;
                  that.$store.state.systemConfig = Data;
                  that.$store.socketUrl = localStorage.socketUrl = Data.webSocketUrl;
                  that.systemInfo = Data;
                  that.$store.state.appVersion = localStorage.appVersion = that.appVersion;
                }
              });
            }
        },
        beforeRouteEnter(to, from, next) {
          next(vm => { 
              vm.getInfo()
          });
        },
        created: function () {
           this.getInfo()
        },
        mounted: function () {
           
            let explorer =navigator.userAgent;
            if (!(explorer.indexOf("Firefox") >= 0 || explorer.indexOf("Chrome") >= 0)) {
               this.show = false;
            } else {
               this.show = true
            }
        }
    }
</script>
<style lang='scss'>
    @import '../assets/main.scss';
    $imgHeight: 4.58em;
    .login {
      height: 100%;
      width: 100%;
      min-width: 1300px;
      background: url('../assets/img/background.jpg') no-repeat;
      background-size: cover;
      display: table;
      font-size: 1em;
      .login-footer{
        position: absolute;
        bottom:0;
        width:100%;
        left:0;
        font-size: 0.95em;
        padding:2em 0
      }
      .login-content{
        display: table-cell;
        padding-top:8em;
       // width:70%;
        vertical-align: middle;
        .login-main{
          width:80%;
          min-height: 20em;
          margin: 0 auto;
          position: relative;
          .login-header{
            width:100%;
            float: left;
            text-align: left;
            img{
              float:left;
              width: 11.52em;
              height: $imgHeight;
              margin-right:1em
            }
            span{
              font-size: 2.6em;
              font-weight: 600;
              letter-spacing: 0.04em;
              text-shadow: 1px 0  9px #18529C, -1px 0  9px #18529C,1px 0   9px #216DC5 ,-1px 0   9px #216DC5 ,0 0  9px #0F6BCE ;
              color: white;
      //        -webkit-text-stroke: 0.3px #18529C;
              display: block;
               line-height: 1.64;
              font-family: -apple-system, BlinkMacSystemFont,
              "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
              "Fira Sans", "Droid Sans", "Helvetica Neue",
              sans-serif;
            }

          }
          .login-header:after{
            @extend .clearElement
          }
          .img-Position{
            width: 59.3em;
            height: 21.88em;
            position: absolute;
            right: -3em;
            top: -9.85em;
            img{
              width: 100%;
              height: 100%
            }
          }
          .login-content{
            height:15.44em;
            margin-top:1.3em;
            width:calc(100% - 15em);
            float:left;
            background: url('../assets/img/login-background.png') no-repeat;
            background-size: contain;
            padding:0 8.5em;
            .input-groups{
              margin: 3% 2em 3% 0;
              float:left;
              .label-show{
                text-shadow: 2px 4px  3px black;
                font-size:1.1em;
                line-height:3;
                letter-spacing: 0.05em;
              }
              input{
                border: 2px solid #42ACF6;
                background: #111F3C!important;
                padding: 0.4em 1.3em;
                height: 3.177em;
                font-size: 1em;
                border-radius: 0.25em;
                box-shadow: 0 0 2em #42ACF6;
              }
              svg{
                font-size: 1.4em
              }
              input.checkBox{
                opacity: 0;
                width: 1.2em;
                margin-top: 0.3em;
                margin-right: 0.5em;
                background: transparent;
              }
              label.myCheckbox{
                top: -1.5em
              }
              label.myCheckbox:before{
                left: -2.2em;
                top: -0.3em;
                width: 1.2em;
                height: 1.2em;
                border: 2px solid #42ACF6;
                border-radius: 10%;
                background: #111F3C;
                line-height: 1.2;
                font-size:1em
              }
              input:checked + .myCheckbox:before{
                line-height: 1.2;
                font-size:1.1em
              }
            }
            .button-div{
              width:calc(100% - 12em);
              float:left;
              text-align: left;
               button{
                 margin: 3% 0 3% 5em;
                 padding: 0.5em 6em;
                 font-size:1em;
                 height: 2.6478em;
               }
          }
          }
        }
        .login-main:after{
          @extend .clearElement
        }
      }
    }
    @media screen and (max-width: 1366px) {
      #app {
        .login-main{
          min-width:1120px
        }
      }
    }
    @media screen and (min-width: 1367px) and (max-width: 1700px) {
      #app {
        .login-main{
          min-width:1240px
        }
      }
    }
    @media screen and (min-width: 1701px) and (max-width: 1920px) {
      #app {
        .login-main{
           min-width:1500px
        }
      }
    }
</style>



